html,
body,
main {
  height: 100%;
}

body {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}

.container {
  padding: 50px 1rem;
}

.btn {
  display: inline-block;
  border: none;
  background: #555;
  color: #fff;
  padding: 7px 6px;
  cursor: pointer;
}

.btn:hover {
  background: #666;
}
.todo-list {
  overflow: scroll;
  height: 600px;
}
.todo-list :hover {
  background-color: #d5d8e5c4;
}

.flash-error {
  background-color: red;
}

.flash-error {
  border: 1px solid black;
  position: fixed;
  text-align: center;
  width: 50%;
  height: 17%;
  padding: 24px;
  display: block;
  left: 25%;
}
.todo-item-name {
  min-width: 180px;
  display: inline-block;
}


input[type=checkbox]:checked + span.todo-item-name{
  text-decoration: line-through;
}

